body {
  background-color: #f2f6ff;
}
.categoryContainer {
  margin-left: 16%;
  margin-right: auto;
  margin-top: 2%;
}

.categoryContainer h1 {
  margin-bottom: 16px;
  border: 2px solid red;
}
.cardContainer {
  display: flex;
  flex-direction: row;
  width:75%;
  /* width: calc(25%-22px); */
  height:auto;
  margin: auto;
  border: 2px solid blueviolet;
}

.cardContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 60px auto 40px auto;
  border: 2px solid firebrick;
  /* margin-top: 40px;
  margin-left: 16%;
  margin-right: 15%; */
}

.filterByCategory {
  display: flex;
  flex-wrap: row;
  font-size: 15px;
}

.filterByCategory h2 {
  font-size: 12px;
  margin-top: 10px;
  margin-bottom: 15px;
  margin-left: 8px;
  font-style: normal;
}

.active {
  background-image: linear-gradient(rgb(0, 131, 255), blue);
  color: white;
}

/* footer */

footer {
  background-color: #172146;
  color: white;
  text-align: center;
  padding: 40px 40px;
  margin-top: 80px;
}
@media only screen and (max-width: 480px) {
  .cardContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: auto;
    margin-top: 40px;
    margin-left: 16%;
    margin-right: 15%;
  }
  footer {
    /* height:300vh; */
    width: 100%;
    background-color: #172146;
    color: white;
    text-align: center;
    padding: 40px 40px;
    margin-top: 80px;
  }
}

@media screen and (min-width: 481px) and (max-width: 720px) {
  .cardContainer {
    border: 2px solid blue;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 40px;
    margin-left: 16%;
    margin-right: 15%;
  }

  footer {
    position: relative;
    top: 1100px;
    background-color: #172146;
    color: white;
    text-align: center;
    padding: 40px 40px;
    margin-top: 80px;
  }
}



@media screen and (min-width: 721px) {
  body {
    background-color: #f2f6ff;
  }

  .cardContainer {
    border: 12px solid forestgreen;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 40px;
    margin-left: 16%;
    margin-right: 15%;
  }
}


@media screen and (min-width: 1081px) {
  body {
    background-color: #f2f6ff;
  }

  .cardContainer {
    border: 5px solid floralwhite;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 40px;
    margin-left: 16%;
    margin-right: 15%;
  }
  footer {
    position: relative;
    top: 500px;
    background-color: #172146;
    color: white;
    text-align: center;
    padding: 40px 40px;
    margin-top: 80px;
  }
}