.category-container {
  margin-left: 16%;
  margin-right: auto;
  margin-top: 2%;
}

.categoryContainer h1 {
  margin-bottom: 16px;
}

.filterByCategory {
  display: flex;
  flex-wrap: row;
  font-size: 15px;
}

.filterByCategory h2 {
  font-size: 12px;
  margin-top: 10px;
  margin-bottom: 15px;
  margin-left: 8px;
  font-style: normal;
}

.category {
  display: inline-block;
  border: 1px solid #0084ff;
  border-radius: 14px;
  margin: 6px 6px;
  padding: 4px 8px;
  color: #0084ff;
}

.category:hover {
  background-image: linear-gradient(269deg, #0048ff, #0096ff);
  color: white;
}

.active {
  background-image: linear-gradient(rgb(0, 131, 255), blue);
  color: white;
}
