* {
  margin: 0px;
  padding: 0px;
  box-sizing: box-border;
  font-family: Raleway, sans-serif;
}

.top-container > div {
}

/* .cardContainer {
  display: flex;
  flex-direction: row;
  width: calc(25%-22px);
  margin: auto;
} */

.card {
  height: 440px;
  max-width: 320px;
  margin: 10px;
  padding: 0px 0px;
  border-radius: 10px;
  background-color: #fff;
  border: 1px solid #0096ff;
}

.cardImg {
  height: 150px;
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.cardTitle {
  font-size: 1.3em;
  font-weight: 600;
  line-height: 1.14;
  color: black;
  font-size: 14px;
}

.cardAuthor {
  font-size: 0.9em;
  font-weight: 500;
  line-height: 2;
  color: #0096ff;
  font-family: Raleway;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  text-align: left;
  margin: 0;
  padding-top: 4px;
}

.cardDesc {
  font-family: Raleway;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.43;
  letter-spacing: 0.35px;
  text-align: left;
  color: #000;
  opacity: 0.5;
  white-space: pre-wrap;
}

.cardDate {
  font-size: 14px;
  font-weight: 300;
  font-style: normal;
  color: #000;
  margin-top: 0.3em;
}

.cardInfo {
  padding-top: 6px;
  padding: 10px;
}

.authorSection {
  height: 200px;
  width: 200px;
}

@media screen and (min-width: 1081px) {
  body {
    background-color: #f2f6ff;
  }

  footer {
    position: relative;
    top: 500px;
    background-color: #172146;
    color: white;
    text-align: center;
    padding: 40px 40px;
    margin-top: 80px;
  }

  .card {
    cursor: pointer;
    box-shadow: 0.5px 0.5px 0.5px skyblue;
    width: 30%;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    border: 2px solid red;
  }
  .cardImg {
    height: 170px;
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }

  .cardInfo {
    font-family: Raleway;
    font-size: 14px;
    font-weight: 400;
    background-color: #fff;
    font-style: normal;
    font-stretch: normal;
    line-height: 0.5%;
    letter-spacing: 0.3px;
    text-align: left;
    white-space: pre-wrap;
    border: 4px solid red;
  }

  .cardTitle {
    font-size: large;
    font-family: Raleway;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    text-align: left;
    color: black;
    padding: 2% 2%;
  }

  .autthorDateSection {
    padding: 2%;
  }

  .authorSection > span {
    padding: 2%;
  }

  .cardDesc {
    padding: 0px 2%;
    font-size: 16px;
    opacity: 0.5;
  }
}

@media screen and (min-width: 781px) and (max-width: 1080px) {
  .card {
    background-color: white;
    width: 35%;
    height: 496px;
    border: 5px solid fuchsia;
  }

  .cardInfo {
    font-family: Raleway;
    font-size: 12px;
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    line-height: 0.5%;
    /* letter-spacing: 0.3px; */
    text-align: left;
    white-space: pre-wrap;
    border: 4px solid blueviolet;
  }
  .cardTitle {
    font-size: 15px;
    padding: 2px 5px;
  }
  .cardDesc {
    font-size: 15.5px;
  }

  .cardInfo > p,
  div {
    padding: 5px 5px;
  }
}
@media screen and (min-width: 381px) and (max-width: 781px) {
  .card {
    background-color: white;
    /* width: 40%;
    height: 500px; */
    border: 5px solid fuchsia;
    height: 410px;
  }
  .cardTitle {
    font-size: 17px;
    padding: 2px 5px;
  }
  .cardAuthor {
    padding: 5px 5px;
  }
  .cardDesc {
    font-size: 15.5px;
    padding: 5px 5px;
  }
}
