nav {
  display: flex;
  flex-direction: row;
  height: 120px;
  background-color: #0096ff;
  width: 100%;
  border-bottom: #0048ff;
}
.nav {
  display: flex;
  flex-direction: row;
  height: 85px;
  width: 100%;
  padding-left: 5%;
  margin-right: auto;
  border-bottom: 2px solid rgb(204, 195, 195);
  background-color: #fff;
}
.left {
  float: left;
  display: flex;
  justify-content: center;
  justify-content: space-between;
  justify-content: space-around;
}
.right {
  position: absolute;
  top: 0px;
  left: 60vw;
  float: right;
  display: flex;
  justify-content: center;
  justify-content: space-between;
}
.header {
  color: gray;
  font-size: 15px;
  padding: 20px 20px;
}
.headerDark {
  color: #000000;
  padding: 15px 1px;
  font-size: large;
  position: absolute;
  top: 10px;
  left: 260px;
}

.headerGray {
  color: gray;
  font-size: 14px;
  margin-right: 6px;
  margin-top: 20px;
}
.headerItalics {
  position: relative;
  top: 45px;
  left: -40px;
  font-style: italic;
  font-size: large;
}
.logo_logo {
  height: 100%;
  margin-top: 5px;
  font-family: Raleway, sans-serif;
  font-size: 22px;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.08;
  letter-spacing: -0.9px;
  text-align: left;
  font-size: 1.8em;
  margin-top: 0;
  width: 130px;
  position: relative;
  top: 10px;
  left: 26px;
}
#logo_light {
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(269deg, #0048ff, #0096ff);
  -webkit-background-clip: text;
}
.headerBtn {
  outline: none;
  margin-top: 5%;
  margin-bottom: 2%;
  margin-left: 2%;
  margin-right: 4%;
  height: 6vh;
  width: 13vw;
  background-image: linear-gradient(-240deg, rgb(0, 131, 255), blue);
  color: white;
  text-align: center;
  border-radius: 22px;
  padding: 4px 14px;
  border: 2px solid rgb(0, 131, 255);
}

@media screen and (min-width: 481px) and (max-width: 481px) {
  body {
    background-color: #f2f6ff;
    margin: auto;
  }
  .headerGray,
  .headerDark {
    display: none;
  }
  .headerBtn {
    border: 2px solid rgb(0, 131, 255);
    outline: none;
    margin-top: 12%;
    margin-bottom: 2%;
    margin-left: 2%;
    margin-right: 6%;
    padding: auto 10px;
    width: 30vw;
    position: relative;
    top: 15px;
    right: -20px;
  }
}

@media only screen and (min-width: 320px) and (max-width: 721px) {
  .headerDark,
  .headerGray {
    display: none;
  }
  /* .logo_logo{
    width:50%;
  } */

  body {
    background-color: #f2f6ff;
  }

  .cardContainer {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    margin-left: 5%;
  }

  .card {
    max-width: 380px;
    height: 300px;
  }
  .headerBtn {
    outline: none;
    margin-top: 7%;
    margin-bottom: 2%;
    margin-left: 0.5%;
    margin-right: 0.5%;
    padding: auto 10px;
    width: 25vw;
    font-size: 12px;
    position: relative;
    right: -20px;
    border: 2px solid rgb(0, 131, 255);
  }
}
